.experience {
    margin-bottom: 5%;

    h3 {
        &.heading-experience {
            margin-top: 0px; } }

    @include element('resume') {
        position: relative;
        margin-top: 30px;
        margin-bottom: 60px;

        .panel-heading {
            padding: 0px 15px; }

        .resume {
            @include element('item') {
                position: relative;
                margin-bottom: 20px;

                &:last-child {
                    margin-bottom: 0px;

                    &:before {
                        display: none; } }

                .panel {
                    width: calc(100% - 210px);
                    margin-left: 210px;
                    padding: 10px;
                    background: transparent;
                    border: 1px solid #6e6e6e;
                    border-radius: 4px;

                    p {
                        margin: 0px; }

                    h5 {
                        margin-top: 0px;
                        opacity: 0.6; }

                    ul {
                        margin-left: 20px; }

                    @include element('title') {
                        h4 {
                            font-weight: 600;
                            text-transform: uppercase;
                            @include fonts('Raleway'); } } }

                .item {
                    @include element('year') {
                        position: absolute;
                        top: 0;
                        left: 0;
                        margin: 0;
                        display: block;
                        width: 120px;
                        height: 40px;
                        margin-bottom: -20px;
                        margin-right: auto;
                        margin-left: auto;
                        line-height: 40px;
                        background-color: $grey;
                        text-align: center;
                        font-weight: 700;
                        text-transform: uppercase;
                        border-radius: 4px;
                        color: $asphalt;
                        font-weight: bold;
                        @include fonts('Open Sans'); }

                    @include element('btn') {
                        position: absolute;
                        z-index: 5;
                        top: 0;
                        left: 150px;
                        width: 40px;
                        height: 40px;
                        background-color: $grey;
                        border-radius: 4px;

                        a {
                            display: block;
                            position: absolute;
                            z-index: 10;
                            top: 0;
                            left: 0;
                            width: 40px;
                            height: 40px;

                            &:before {
                                display: block;
                                position: absolute;
                                z-index: 15;
                                content: '-';
                                top: 2px;
                                left: 0;
                                width: 40px;
                                height: 40px;
                                line-height: 38px;
                                color: $asphalt;
                                font-size: 30px;
                                font-weight: 700;
                                text-align: center; }

                            &.collapsed:before {
                                content: '+'; } } } } } } } }

@media (min-width: 568px) {
    .experience {
        @include element('resume') {
            .resume {
                @include element('item') {
                    margin-bottom: 20px;

                    .panel {
                        width: calc(100% - 140px);
                        margin-left: 140px; }

                    .item {
                        @include element('year') {
                            position: absolute;
                            top: 0;
                            left: 0;
                            margin: 0; } } } } } } }

@media (min-width: 768px) {
    .experience {
        @include element('resume') {
            .resume {
                @include element('item') {
                    &:before {
                        position: absolute;
                        content: '';
                        width: 1px;
                        height: calc(100% + 20px);
                        top: 0;
                        left: 170px;
                        border-left: 1px dashed #484848; }

                    .panel {
                        width: calc(100% - 210px);
                        margin-left: 210px; } } } } } }
